import classes from "./About.module.scss";
import { Parallax } from "react-parallax";
const data = require("./About.json");

function About() {
  const Container = () => (
    <Parallax
      blur={2}
      bgImage="/pictures/Leikie3.jpg"
      bgImageAlt="the concert"
      strength={300}
      bgClassName={classes.background}
    >
      <div className={classes.content}>
        <div className={classes.gradiant} />
        <div className={classes.mainHeader}>Leah Glick</div>
        <div className={classes.secondHeader}>Violist</div>
      </div>
    </Parallax>
  );
  return (
    <div className={classes.home}>
      <Container />
      <div className={classes.textWrapper}>
        <div className={classes.text}>{data.col0}</div>
        <div className={classes.text}>{data.col1}</div>
        <div className={classes.text}>{data.col2}</div>
      </div>
    </div>
  );
}

export default About;
