import classes from "./Contact.module.scss";
import { Parallax } from "react-parallax";

function Contact() {
  const Container = () => (
    <Parallax
      blur={0}
      bgImage="/pictures/contact.JPG"
      bgImageAlt="contact picture"
      strength={300}
      bgClassName={classes.background}
    >
      <div className={classes.content}>
        <div className={classes.gradiant} />
        <div className={classes.mainHeader}>Contact</div>
      </div>
    </Parallax>
  );
  return (
    <div className={classes.contact}>
      <Container />
      <div className={classes.textWrapper}>
        <div className={classes.contactItem}>leikieglick@gmail.com</div>
        <div className={classes.contactItem}>facebook.com/leikie.glick</div>
        <div className={classes.contactItem}></div>
        <div className={classes.contactItem}></div>
      </div>
    </div>
  );
}

export default Contact;
